import React from "react";

const ErrorIndicator = () => {
  return (
    <div>
      <div>Что-то пошло не так</div>
      <div>
        Попробуйте обновить вкладку браузера или повторите попытку через
        несколько минут
      </div>
    </div>
  );
};

export default ErrorIndicator;
